
.anchor {
  background: #454545;
}
.fossil {
  background: #7C7774;
}
.rice {
  background: #F5F5F5;
}
.frost {
  background: #E5ECF3;
}
.eggshell {
  background: #FEFCE8;
}
.salt {
  background: #EFE8F6;
}
.chiffon {
  background: #EEF5E6;
}
.white {
  background: white;
}
.footer-scroll {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.footer-scroll.footer-swiper {
    padding-bottom: 30%;
    height: 0px;
}
@media screen and (max-width: 1280px) {
.footer-scroll.footer-swiper {
        padding-bottom: 350px;
}
}
.footer-scroll.modal-swiper {
    height: 100%;
    padding-bottom: 0px;
}
.main-container {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  position: absolute;
  top: 0;
  margin: auto;
  max-width: 100%;
}
.main-container.modal-swiper {
    position: relative;
    width: 100%;
    height: 458px;
}
@media screen and (max-width: 1000px) {
.main-container.modal-swiper {
        height: 300px;
}
}
@media screen and (max-width: 760px) {
.main-container.modal-swiper {
        height: 200px;
}
}
.main-container.popup-swiper, .main-container.popup-swiper-press {
    width: 100%;
    height: calc(100% - 48px);
}
.main-container.footer-swiper {
    margin-right: 0px;
    height: calc(100% - 48px);
    width: 100%;
}
.swiper-container {
  width: 100% !important;
  height: 100%;
  overflow: visible !important;
}
.buttons-container {
  float: right;
  margin-right: 4px;
  margin-top: 12px;
}
.buttons-container.footer-swiper {
    margin-right: 96px;
}
@media screen and (max-width: 760px) {
.buttons-container.footer-swiper {
        margin-right: 24px;
}
}
@media screen and (min-width: 1281px) {
.buttons-container.footer-swiper {
        margin-right: 7.5%;
}
}
.text-area {
  margin-top: 12px;
  text-align: left;
}
.text-line {
  width: 100%;
  height: 1px;
  background: #7C7774;
  margin-top: 12px;
}
.swiper-slide.footer-swiper {
  width: 45%;
}
@media screen and (max-width: 1280px) {
.swiper-slide.footer-swiper {
      width: 500px;
}
}
.swiper-slide.modal-swiper.full, .swiper-slide.popup-swiper.full, .swiper-slide.popup-swiper-press.full {
  width: 100%;
}
.swiper-slide.modal-swiper.half, .swiper-slide.popup-swiper.half, .swiper-slide.popup-swiper-press.half {
  width: 50%;
}
@media screen and (max-width: 760px) {
.swiper-slide {
    width: 100% !important;
}
}
.swiper-slide.swiper-spacer {
  width: calc(45% + 128px) !important;
}
@media screen and (min-width: 1281px) {
.swiper-slide.swiper-spacer {
      width: calc(45% + 10%) !important;
}
}
.swiper-slide.swiper-spacer.modal-swiper, .swiper-slide.swiper-spacer.popup-swiper, .swiper-slide.swiper-spacer.popup-swiper-press {
    display: none;
}
@media screen and (max-width: 760px) {
.swiper-slide.swiper-spacer {
      width: 0px;
      display: none;
}
}
.hide-me {
  opacity: 0;
  pointer-events: none;
}
.swiper-text {
  pointer-events: none;
  margin-top: 12px;
}
.cover {
  position: relative;
  width: 100%;
  height: 100%;
}
.double-image {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.double-inner {
  position: relative;
  width: calc(50% - 6px);
  height: 100%;
}



